:root{
  --dark:#000814;
  --medium : #001d3d;
  --light: #003566;
  --yellow: #ffc300;
  --lightyellow: #ffd60a;
  --shadowdark:1px 1px 5px 1px var(--dark);
  --shadowlight:1px 1px 5px 1px var(--light);
}


*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  transition: transform 0.5s ease;
}
.App{
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  /* display: grid;
  grid-template-columns: 1fr;
  justify-content: center;
  align-items: center; */
  background-color: var(--dark);
  min-height: 100vh;
  
}
.headerContainer{

width: 100%;
height: 50px;

text-align: center;
background-color: var(--yellow);
color: var(--light);
}
header {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  height:100%; 
}
.main{
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  justify-content: center;
  align-items: flex-start;
  justify-items: center;
  width: 100%;
  max-width: 1200px;
  height: 100%;
}

.left{
  display: flex;
  justify-content: center;
  /* justify-items: center; */
  align-items: center;
  width: 100%;
  height: 100%;
  /* border: 1px solid white; */
  padding: 10px;
}
.buttons{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  text-align: center;
  gap: 10px;
}
.buttons button,select{
  width:100%;
}
.buttons button,select,label,input,.mapcont button{
  padding: 10px;
  border-radius: 5px;
  background-color: var(--lightyellow);
}
.buttons button,.mapcont button{
  border: none;
}
.buttons form{
  /* margin: 10px; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
}
.buttons button:hover,select:hover,select option:hover,input:hover,.mapcont button:hover{
  cursor: pointer;
  background-color: var(--light);
  color: var(--lightyellow);
  box-shadow: 1px 1px 5px 1px var(--lightyellow);
}
.mapcont{
  width: 500px;
  height: 500px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  justify-content: center;
  align-items: center;
  margin: 20px auto;
  
}

.map{
  width: 500px;
  height: 500px;
  box-shadow: 1px 1px 5px 1px var(--lightyellow);
  /* padding: 10px; */
  /* overflow: scroll; */
}


.right{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
  padding: 10px;
  /* border: 1px solid white; */
  height: 100%;
 
  width: 100%;
}

.orders{
  width: 100%;
  border-radius: 5px;
  align-self: center;
  text-align: center;
  box-shadow: 1px 1px 5px 1px var(--light);
  background-color: var(--lightyellow);
}
.orders h2{
  border-bottom: 1px solid var(--dark);
}
.orders .list{

  max-height: 400px;
  min-height: 300px;
  overflow-y: scroll;
}
.list h5{
  height: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.list article{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 5px;
  padding: 10px;
  box-shadow: 1px 1px 5px 1px var(--light);
  height: 100%;
  border-radius: 5px;
}
.list article button{
  padding: 5px;
  border: none;
  background-color: var(--medium);
  color: var(--yellow);
  margin: 5px;
  border-radius: 5px;
}
.list article button:hover{
  cursor: pointer;
  box-shadow: var(--shadowdark);

}
.delivered:hover{
  background-color: green;
}
.canceled:hover{
  background-color: red;
}
.buttons p{
  display: none;
}


@media screen and (min-width:1000px) {
  .left{
    transform: translateX(0) !important;
  }
}

@media screen and (max-width:1000px) {
  .left{
    display: none;
    transform: translateX(-500px);
    z-index: 99999;
    align-self: center;
    justify-self: self-start;
    height: 100%;
    background-color: var(--dark);
    display : block;
    position : fixed;
    top : 0;
    
    width : 50%;
    height : 100%;
  }
  .buttons p{
    display: block;
    color: red;
  }
  .main{
    grid-template-columns: 2fr 1fr;
  }
  header{
    justify-content: space-between;
    margin: auto 20px;
  }
  header div:hover{
    transform: scale(1.1);
  }
  .line{
    width: 20px;
    margin: 2px;
    /* opacity: 0.8; */
    height: 2px;
    background-color: var(--light);
  }
  
}
@media screen and (max-width:768px){
  .main{
    display: grid;
    grid-template-columns: 1fr;
    /* grid-template-rows: 2fr 1fr ; */
  }
  .right{
    width: 80%;
  }
}
@media screen and (max-width:520px){
  
  .mapcont,.map{
    width: 80%;
    height: 300px;
  }
  .left input{
    width: 100%;
  }
}
@media screen and (max-width:350px){
